<template>
  <view class="month-view">
    <!-- 顶部导航栏 -->
    <view class="calendar-header">
      <button class="nav-btn" @click="prevMonth">← 上月</button>
      <text class="header-title">{{ currentYear }}年 {{ currentMonth + 1 }}月</text>
      <button class="nav-btn" @click="nextMonth">下月 →</button>
    </view>

    <!-- 星期头 -->
    <view class="week-header">
      <view v-for="(day, index) in weekDays" :key="index" class="week-day">{{ day }}</view>
    </view>

    <!-- 日期网格 -->
    <view class="calendar-grid">
      <view
        v-for="(day, index) in monthDays"
        :key="index"
        class="calendar-cell"
        :class="{ 
          today: isToday(day.date),
          'not-current': !day.inCurrentMonth 
        }"
        @dblclick="openAddDialog(day.date)"
      >
        <text class="date-label">{{ day.date.getDate() }}</text>
        <view class="events">
          <view
            v-for="(event, i) in getEventsForDay(day.date)"
            :key="i"
            class="event-item"
            @click="openEvent(event)"
          >
            <text class="event-title">{{ event.title }}</text>
          </view>
        </view>
      </view>
    </view>

    <!-- 事件对话框 -->
    <view class="dialog" v-if="showDialog">
      <view class="dialog-mask" @click="closeDialog"></view>
      <view class="dialog-content">
        <view v-if="!isEditing">
          <view class="dialog-header">
            <text class="dialog-title">事件详情</text>
          </view>
          <view class="dialog-body">
            <view class="event-field">
              <text class="field-label">标题:</text>
              <text>{{ selectedEvent.title }}</text>
            </view>
            <view class="event-field">
              <text class="field-label">开始时间:</text>
              <text>{{ formatDate(selectedEvent.startTime) }}</text>
            </view>
            <view class="event-field">
              <text class="field-label">结束时间:</text>
              <text>{{ formatDate(selectedEvent.endTime) }}</text>
            </view>
            <view v-if="selectedEvent.description" class="event-field">
              <text class="field-label">描述:</text>
              <text>{{ selectedEvent.description }}</text>
            </view>
            <view v-if="selectedEvent.location" class="event-field">
              <text class="field-label">地点:</text>
              <text>{{ selectedEvent.location }}</text>
            </view>
          </view>
          <view class="dialog-footer">
            <button @click="editEvent">编辑</button>
            <button @click="deleteEventConfirm">删除</button>
            <button @click="closeDialog">关闭</button>
          </view>
        </view>
        
        <view v-else>
          <view class="dialog-header">
            <text class="dialog-title">编辑事件</text>
          </view>
          <view class="dialog-body">
            <view class="form-group">
              <label>标题:</label>
              <input v-model="editingEvent.title" placeholder="输入标题" />
            </view>
            <view class="form-group">
              <label>开始时间:</label>
              <input v-model="editingEvent.startTime" placeholder="YYYY-MM-DD HH:mm" />
            </view>
            <view class="form-group">
              <label>结束时间:</label>
              <input v-model="editingEvent.endTime" placeholder="YYYY-MM-DD HH:mm" />
            </view>
            <view class="form-group">
              <label>描述:</label>
              <textarea v-model="editingEvent.description" placeholder="输入描述"></textarea>
            </view>
            <view class="form-group">
              <label>地点:</label>
              <input v-model="editingEvent.location" placeholder="输入地点" />
            </view>
          </view>
          <view class="dialog-footer">
            <button @click="saveEvent">保存</button>
            <button @click="cancelEdit">取消</button>
          </view>
        </view>
      </view>
    </view>

    <!-- 添加事件对话框 -->
    <view class="dialog" v-if="showAddDialog">
      <view class="dialog-mask" @click="closeAddDialog"></view>
      <view class="dialog-content">
        <view class="dialog-header">
          <text class="dialog-title">添加事件</text>
        </view>
        <view class="dialog-body">
          <view class="form-group">
            <label>标题:</label>
            <input v-model="newEvent.title" placeholder="输入标题" />
          </view>
          <view class="form-group">
            <label>开始时间:</label>
            <input v-model="newEvent.startTime" placeholder="YYYY-MM-DD HH:mm" />
          </view>
          <view class="form-group">
            <label>结束时间:</label>
            <input v-model="newEvent.endTime" placeholder="YYYY-MM-DD HH:mm" />
          </view>
          <view class="form-group">
            <label>描述:</label>
            <textarea v-model="newEvent.description" placeholder="输入描述"></textarea>
          </view>
          <view class="form-group">
            <label>地点:</label>
            <input v-model="newEvent.location" placeholder="输入地点" />
          </view>
        </view>
        <view class="dialog-footer">
          <button @click="addEvent">添加</button>
          <button @click="closeAddDialog">取消</button>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
import { 
  getEvents, 
  addEvent, 
  updateEvent, 
  deleteEvent 
} from '../api/event.js';
import { 
  getMonthDays, 
  isToday, 
  formatDate,
  getFirstDayOfMonth,
  getLastDayOfMonth
} from '../utils/date.js';
import { scheduleReminders, scheduleSingleReminder } from '../utils/reminder.js';

export default {
  props: {
    events: {
      type: Array,
      default: () => []
    }
  },
  emits: ['refresh'],
  data() {
    return {
      currentDate: new Date(),
      weekDays: ['日', '一', '二', '三', '四', '五', '六'],
      showDialog: false,
      showAddDialog: false,
      selectedEvent: null,
      isEditing: false,
      editingEvent: {},
      newEvent: {
        title: '',
        description: '',
        startTime: '',
        endTime: '',
        location: '',
        reminder: { value: 10, unit: 'm' }
      },
      addDate: new Date()
    };
  },
  computed: {
    currentYear() {
      return this.currentDate.getFullYear();
    },
    currentMonth() {
      return this.currentDate.getMonth();
    },
    monthDays() {
      const days = getMonthDays(this.currentDate);
      const firstDay = getFirstDayOfMonth(this.currentDate);
      const lastDay = getLastDayOfMonth(this.currentDate);
      
      return days.map(date => {
        const inCurrentMonth = date >= firstDay && date <= lastDay;
        const dayEvents = this.getEventsForDay(date);
        return {
          date,
          inCurrentMonth,
          events: dayEvents.slice(0, 3), // 只显示前3个事件
          hasMoreEvents: dayEvents.length > 3
        };
      });
    }
  },
  watch: {
    events: {
      handler() {
        scheduleReminders(this.events);
      },
      immediate: true
    }
  },
  methods: {
    isToday,
    formatDate,
    prevMonth() {
      this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() - 1, 1);
    },
    nextMonth() {
      this.currentDate = new Date(this.currentDate.getFullYear(), this.currentDate.getMonth() + 1, 1);
    },
    getEventsForDay(date) {
      return this.events.filter(event => {
        const eventDate = new Date(event.startTime);
        return isToday(eventDate, date);
      });
    },
    openEvent(event) {
      this.selectedEvent = { ...event };
      this.isEditing = false;
      this.showDialog = true;
    },
    closeDialog() {
      this.showDialog = false;
      this.selectedEvent = null;
    },
    editEvent() {
      this.editingEvent = { ...this.selectedEvent };
      this.isEditing = true;
    },
    cancelEdit() {
      this.isEditing = false;
    },
    async saveEvent() {
      try {
        await updateEvent(this.editingEvent._id, this.editingEvent);
        this.isEditing = false;
        this.showDialog = false;
        this.$emit('refresh');
        uni.showToast({ title: '更新成功' });
      } catch (err) {
        uni.showToast({ title: '更新失败', icon: 'none' });
      }
    },
    deleteEventConfirm() {
      uni.showModal({
        title: '确认删除',
        content: '确定要删除此事件吗？',
        success: (res) => {
          if (res.confirm) {
            this.deleteEvent();
          }
        }
      });
    },
    async deleteEvent() {
      try {
        await deleteEvent(this.selectedEvent._id);
        this.showDialog = false;
        this.$emit('refresh');
        uni.showToast({ title: '删除成功' });
      } catch (err) {
        uni.showToast({ title: '删除失败', icon: 'none' });
      }
    },
    openAddDialog(date) {
      this.addDate = new Date(date);
      this.newEvent = {
        title: '',
        description: '',
        startTime: this.formatToAddTime(date),
        endTime: this.formatToAddTime(date, 60),
        location: '',
        reminder: { value: 10, unit: 'm' }
      };
      this.showAddDialog = true;
    },
    formatToAddTime(date, addMinutes = 0) {
      const d = new Date(date);
      d.setHours(9, 0, 0, 0); // 默认时间设为上午9点
      d.setMinutes(d.getMinutes() + addMinutes);
      return `${d.getFullYear()}-${String(d.getMonth() + 1).padStart(2, '0')}-${String(d.getDate()).padStart(2, '0')} ${String(d.getHours()).padStart(2, '0')}:${String(d.getMinutes()).padStart(2, '0')}`;
    },
    closeAddDialog() {
      this.showAddDialog = false;
    },
    async addEvent() {
      try {
        await addEvent(this.newEvent);
        this.showAddDialog = false;
        this.$emit('refresh');
        uni.showToast({ title: '添加成功' });
        scheduleSingleReminder(this.newEvent);
      } catch (err) {
        uni.showToast({ title: '添加失败', icon: 'none' });
      }
    }
  }
};
</script>

<style scoped>
.month-view {
  padding: 10px;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.nav-btn {
  padding: 5px 10px;
  background-color: #f0f0f0;
  border: none;
  border-radius: 4px;
}

.header-title {
  font-size: 18px;
  font-weight: bold;
}

.week-header {
  display: flex;
  background-color: #f5f5f5;
  font-weight: bold;
}

.week-day {
  flex: 1;
  text-align: center;
  padding: 10px 0;
}

.calendar-grid {
  display: flex;
  flex-wrap: wrap;
}

.calendar-cell {
  width: 14.28%;
  min-height: 100px;
  border: 1px solid #eee;
  padding: 5px;
  position: relative;
}

.today {
  background-color: #e6f7ff;
}

.not-current {
  opacity: 0.5;
}

.date-label {
  font-weight: bold;
  margin-bottom: 5px;
}

.events {
  font-size: 12px;
}

.event-item {
  background-color: #1890ff;
  color: white;
  padding: 2px 4px;
  border-radius: 2px;
  margin-bottom: 2px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
}

.dialog-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.dialog-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-height: 80%;
  background-color: white;
  border-radius: 8px;
  overflow: hidden;
}

.dialog-header {
  padding: 15px;
  border-bottom: 1px solid #eee;
  text-align: center;
}

.dialog-title {
  font-size: 18px;
  font-weight: bold;
}

.dialog-body {
  padding: 15px;
  max-height: 60vh;
  overflow-y: auto;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.form-group input,
.form-group textarea {
  width: 100%;
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box;
}

.event-field {
  margin-bottom: 10px;
}

.field-label {
  font-weight: bold;
  margin-right: 10px;
}

.dialog-footer {
  padding: 15px;
  border-top: 1px solid #eee;
  display: flex;
  justify-content: space-around;
}

.dialog-footer button {
  padding: 8px 16px;
  background-color: #1890ff;
  color: white;
  border: none;
  border-radius: 4px;
}
</style>